export default {
    data() {
        return {
            time:'',
            fll:true
        }
    },
    onLoad() {
        var that = this
        uni.request({
            url:this.url + 'usermachine/recode',
            method:'GET',
            header:{
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                that.time = res.data.data
                that.time = time.reverse()
                console.log(that.time)
                if(that.time.length == 0) {
                    that.fll = false
                }else {
                    that.fll = true
                }
            }
        })
    },
    methods:{

    }
}
